body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.main{
  padding: 20px;
}
button{
  text-transform: uppercase;
  font-size: 16px;
  color: #000;
  cursor: pointer;
  -webkit-box-shadow: none;
          box-shadow: none;
  border: 1px solid #546de5;
